.rmc-tabs {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden;
  height: 100%;
  width: 100%;
}
.rmc-tabs * {
  box-sizing: border-box;
}
.rmc-tabs-content-wrap {
  display: flex;
}
.rmc-tabs-content-wrap-animated {
  transition: transform 0.3s cubic-bezier(0.35, 0, 0.25, 1);
  will-change: transform;
}
.rmc-tabs-pane-wrap {
  width: 100%;
  flex-shrink: 0;
  overflow-y: auto;
}
.rmc-tabs-pane-wrap-active {
  height: auto;
}
.rmc-tabs-pane-wrap-inactive {
  height: 0;
  overflow: visible;
}
.rmc-tabs-tab-bar-wrap {
  flex-shrink: 0;
}
.rmc-tabs-top .rmc-tabs-tab-bar-wrap {
  border-bottom: 1px #eee solid;
}
.rmc-tabs-bottom .rmc-tabs-tab-bar-wrap {
  border-top: 1px #eee solid;
}
.rmc-tabs-tab-bar {
  position: relative;
  display: flex;
  flex-shrink: 0;
  width: 100%;
  overflow: visible;
  z-index: 1;
}
.rmc-tabs-tab-bar-content {
  display: flex;
  width: 100%;
}
.rmc-tabs-tab-bar-prevpage:before {
  pointer-events: none;
  position: absolute;
  top: 0;
  display: block;
  width: 59px;
  height: 100%;
  content: ' ';
  z-index: 1;
  left: 0;
  background: linear-gradient(to right, #ffffff, rgba(255, 255, 255, 0));
}
.rmc-tabs-tab-bar-nextpage:after {
  pointer-events: none;
  position: absolute;
  top: 0;
  display: block;
  width: 59px;
  height: 100%;
  content: ' ';
  z-index: 1;
  right: 0;
  background: linear-gradient(to right, rgba(255, 255, 255, 0), #ffffff);
}
.rmc-tabs-tab-bar-tab {
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  padding: 8px 0;
  font-size: 14px;
  line-height: 14px;
}
.rmc-tabs-tab-bar-tab-active {
  color: #108ee9;
}
.rmc-tabs-tab-bar-underline {
  position: absolute;
  bottom: 0;
  border: 1px #108ee9 solid;
}
.rmc-tabs-tab-bar-animated .rmc-tabs-tab-bar-content {
  transition: transform 0.3s cubic-bezier(0.35, 0, 0.25, 1);
  will-change: transform;
}
.rmc-tabs-tab-bar-animated .rmc-tabs-tab-bar-underline {
  transition: left 0.3s cubic-bezier(0.35, 0, 0.25, 1), color 0.3s cubic-bezier(0.35, 0, 0.25, 1), width 0.3s cubic-bezier(0.35, 0, 0.25, 1);
  will-change: left, width, color;
}



